import {Component} from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams} from 'ionic-angular';
import {ModalAddFamily} from "./add-family/add-family";
import {AngularFireAuth} from 'angularfire2/auth'
import {MembersProf} from "../../../providers/memberProf/members-prof";



@IonicPage()
@Component({
  selector: 'page-family-create',
  templateUrl: 'family-create.html',
})
export class FamilyCreatePage {
  private  familyinfo : any;
    private uid: any;


  constructor(
      public navCtrl: NavController,
      public navParams: NavParams,
      private modalCtrl: ModalController,
      private afs: AngularFireAuth,
      private family: MembersProf,

  ) {
this.loadData()


  }
    loadData() {

        this.afs.authState.subscribe(res => {
            if (res && res.uid) {
                this.uid = res.uid
                this.family.query_family(res.uid).then((res: any) => {
                    this.familyinfo = JSON.parse(res.data)
                    console.log("Family"+this.familyinfo)

                }).catch(er => console.log(er.message))
            } else {
            }
        });


    }
    addFam(){
        let modal = this.modalCtrl.create(ModalAddFamily);
        modal.present();
    }




}



